*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle; }

a:link,
a:visited {
  text-decoration: none;
  transition: all .4s; }

body {
  font-size: 16px;
  font-family: 'Noto Sans TC', sans-serif;
  color: #000;
  overflow-x: hidden;
  line-height: 1.5; }

.container {
  max-width: 1396px;
  padding: 0 50px;
  margin: 0 auto; }

.header {
  background-color: #aa0601; }
  .header nav {
    display: flex;
    justify-content: space-between;
    align-items: center; }

.logoBox {
  max-width: 80px; }

.menu_lists {
  display: flex; }
  .menu_lists li {
    list-style: none;
    margin: 25px 2rem; }
    .menu_lists li a {
      color: #fff;
      font-size: 1.25rem; }
      .menu_lists li a:hover {
        opacity: .8; }
    .menu_lists li:first-child {
      margin-left: 0; }
    .menu_lists li:last-child {
      margin-right: 0; }

footer {
  background-color: #aa0601;
  padding: 3rem 0 2rem;
  color: #fff; }
  footer li {
    list-style: none; }
  footer a {
    color: #fff; }
    footer a:hover {
      opacity: .8; }

footer .material-icons-outlined {
  font-size: 2.25rem;
  margin-right: 22px; }

.footer_linksbox {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.footer_links {
  display: flex; }
  .footer_links li {
    margin-right: 3rem; }
    .footer_links li:last-child {
      margin-right: 0; }

.footer_social {
  display: flex; }
  .footer_social div {
    width: 3rem;
    margin-left: 2rem; }

.footer_info {
  margin: 24px 0 0;
  padding-bottom: 24px;
  border-bottom: 1px solid #fff; }
  .footer_info li {
    margin-bottom: 8px;
    display: flex;
    align-items: center; }
  .footer_info a {
    font-size: 2.25rem;
    font-weight: 300; }

.footer_copyright {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 32px 0 0; }
  .footer_copyright div {
    font-family: 'Roboto'; }
    .footer_copyright div a {
      font-family: 'Noto Sans TC';
      font-size: 14px;
      display: inline-block;
      margin-left: 3rem; }
      .footer_copyright div a:first-child {
        margin-left: 0; }
